/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import "./layout.sass"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          description
        }
      }
    }
  `)

  return (
    <>
      
      <div
      >
        <main>{children}
        
        <footer>
        <div className="social-icons">
            <a href="https://github.com/leancer" ><i className="devicon-github-plain-wordmark" style={{color:"white"}} ></i></a>
            <a href="https://twitter.com/leancerrr"><i className="devicon-twitter-plain colored"></i></a>
            <a href="https://gitlab.com/leancer"><i className="devicon-gitlab-plain-wordmark colored"></i></a>
          </div>
        </footer>
        </main>
        
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
